import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";
export var api = createApi({
    baseQuery: fetchBaseQuery({ baseUrl: "/alinkedin/", credentials: "include" }),
    reducerPath: "main",
    tagTypes: ["activity", "test"],
    endpoints: function (build) { return ({
        //Array<GetActivityResponse>
        getActivity: build.query({
            query: function () { return "/activity"; },
            providesTags: ["activity"],
        }),
    }); },
});
export var useGetActivityQuery = api.useGetActivityQuery;

